// ---------------------------------------------------------------------------------------------------------

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
  padding: 0.8rem 2rem;
  position: sticky;
  top: 40px;
  z-index: 1000;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);

  .logo {
    .navbar-logo {
      height: 45px;
    }
  }

  .menu-icon {
    display: none;
    font-size: 1.5rem;
    cursor: pointer;
    // color: #fff;
  }

  .nav-links {
    display: flex;
    list-style: none;
    gap: 2rem;
    position: relative;

    li {
      position: relative;

      a,
      button {
        background: none;
        border: none;
        color: black;
        font-size: 1rem;
        cursor: pointer;
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 0.4rem;
        transition: color 0.3s;

        &:hover {
          color: #ffb703;
          text-decoration: underline;
        }
      }

      .dropdown-menu {
        position: absolute;
        top: 100%;
        left: 0;
        background: white;
        padding: 0.8rem 1rem;
        display: flex;
        flex-direction: column;
        gap: 0.6rem;
        width: 200px;
        // width: auto;
        border-radius: 6px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);

        a {
          // color: #fff;
          font-size: 0.9rem;
          padding: 0.5rem 1rem;
          text-align: left;
          color: black;
          width: 100%;

          &:hover {
            color: #ffb703;
          }
        }

        // sub-dropdown
        .sub-dropdown {
          position: relative;

          .sub-menu {
            position: red;
            top: 0;
            left: 100%;
            background: #fff;
            border-radius: 6px;
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
            display: flex;
            flex-direction: column;
            min-width: 180px;
            padding: 0.5rem 1rem;

            a {
              color: black;
              font-size: 0.9rem;
    padding: 0.5rem 1rem;
              text-align: left;
              // color: #333;
              width: 100%;

              &:hover {
                color: #ffb703;
              }
            }
          }
        }
      }
    }
  }

  /* Mobile */
  @media (max-width: 768px) {
    .menu-icon {
      display: block;
      z-index: 110; // keeps icon above overlay
    }

    .nav-links {
      // position: absolute;
      position: fixed;
      top: 70px;
      // right: 0;

      //  top: 0;
      left: 0;
      background: white;
      flex-direction: column;
      width: 100%;
      padding: 1rem;
      // justify-content: center;
      // align-items: center;
      transform: translateX(100%);
      transition: transform 0.3s ease-in-out;

      &.open {
        transform: translateX(0);
      }

      li {
        // margin: 0.8rem 0;

        .dropdown-menu {
          position: relative;
          top: 0;
          left: 0;
          box-shadow: none;
          // background: #174b91;
          background: white;
        }
      }
    }
  }
}

